// import { RX__toggle_poss } from "./ui"
import { v1_domain_suggestions, v1_word_thesaurus } from "src/redux/actions/api/v1"
import CONSOLE from "src/console"

/*
 *
 * INPUTS
 *
 */
export function RX__clear_inputs() {
  return (dispatch) => {
    /*
     * reset all reducer properties starting with "input_"
     */
    dispatch({
      type: "RX__clear_inputs",
      data: ""
    })
  }
}
// set multiple at once, to prevent multiple UI re-renders
export function RX__set_inputs(inputs = {}) {
  return (dispatch) => {
    /*
     * in reducer, properties starting with "input_"
     * pass them here as keys, omitting the prefix "input_"
     */
    dispatch({
      type: "RX__set_inputs",
      data: inputs
    })
    /*
     * special case, side-effect when setting "tld"
     */
    if (inputs.tld) {
      dispatch({
        type: "RX__tld_user",
        data: inputs.tld
      })
    }
  }
}

/*
 *
 * SEARCH: trigger
 *
 */
export function RX__search_now() {
  return (dispatch) => {
    /*
     * The search action is determined in the page which is currently in view -
     * - but when to execute the action is triggered from Search.js component.
     */
    dispatch({
      type: "RX__search_now"
    })
  }
}

/*
 *
 * SEARCH: thesaurus
 *
 */
let RX__search_thesaurus__in_progress = false
export function RX__search_thesaurus(options = {}) {
  CONSOLE.info("RX__search_thesaurus()", options)
  return (dispatch, getState) => {
    /*
     * arguments
     */
    let { input, output } = getState()
    if (!input.input_str) {
      console.error("ignore search_thesaurus() because !input.input_str")
      return
    }

    /*
     * prevent redundant calls
     */
    if (output.chunks[input.input_str] && !options.force) {
      console.error("ignore search_thesaurus() because output.chunks[input.input_str]")
      return
    }

    /*
     * prevent simultaneous calls
     */
    if (RX__search_thesaurus__in_progress) {
      console.error("ignore search_thesaurus() because input.in_progress")
      return
    }
    RX__search_thesaurus__in_progress = true

    /*
     * send request
     */
    v1_word_thesaurus(input.input_str).then(function (row = {}) {
      setTimeout(function () {
        RX__search_thesaurus__in_progress = false
      }, 1000)

      /*
       * got response
       */
      dispatch({
        type: "RX__add_chunk",
        data: row || {}
      })
    })
  }
}

/*
 *
 * SEARCH: suggestions
 *
 */
export function RX__search_suggestions(options) {
  return (dispatch, getState) => {
    /*
     * save options
     */
    if (options) {
      dispatch({
        type: "RX__set_suggestions_options",
        data: options
      })
    } else {
      options = {}
    }

    /*
     * arguments
     */
    let { input } = getState()
    if (!input.input_str) {
      console.error("ignore search_suggestions() because !input.input_str")
      return
    }
    if (!input.input_tld) {
      input.input_tld = "com"
    }

    /*
     * data
     */
    let params = {
      phrase: input.input_str,
      tlds_use: [input.input_tld],
      tlds_ignore: [],
      captcha_response: input.input_captcha,
      options: { ...input.suggestions_options, ...options }
    }
    console.log("RX__search_suggestions()", params)

    /*
     * prevent redundant calls
     */
    let params_stringified = JSON.stringify(params)
    if (params_stringified === input.params_stringified) {
      console.error("ignore search_suggestions() because params_stringified === input.params_stringified")
      return
    }
    dispatch({
      type: "RX__params_stringified",
      data: params_stringified
    })

    /*
     * prevent simultaneous calls
     * IMPORTANT: this valudation (return) must be the last validation before the API request,
     * because if "in_progress" flag gets set, but the API does not execute, app will stall.
     */
    if (input.in_progress) {
      console.error("ignore search_suggestions() because input.in_progress")
      return
    }
    dispatch({
      type: "RX__in_progress",
      data: true
    })

    /*
     * clear data: set after get back responses
     */
    dispatch({
      type: "RX__input_spellchecked",
      data: ""
    })
    dispatch({
      type: "RX__domain_suggestions",
      results: {
        domains: {},
        wip_domains: [],
        phrases: [],
        word_hacks: [],
        phrase_lists: []
      }
    })

    /*
     * send request
     */
    v1_domain_suggestions(params)
      .then(async function (results = {}) {
        // end request
        dispatch({
          type: "RX__in_progress",
          data: false
        })

        /*
         * got response
         */
        // tld - always set, even if already specified,
        // because this one is parsed from the query phrase.
        // parsed value overrides value specified by dropdown
        // dispatch(RX__set_inputs({ tld: results.tld }))

        // phrase - spellchecked str
        dispatch({
          type: "RX__input_spellchecked",
          data: results.string
        })

        // dictionary of DB rows per word
        dispatch({
          type: "RX__chunks",
          data: results.chunks_rows
        })

        // the main attraction
        dispatch({
          type: "RX__domain_suggestions",
          results: {
            domains: results.domains,
            wip_domains: results.wip_domains,
            phrases: results.phrases,
            phrase_lists: results.phrase_lists,
            word_hacks: results.word_hacks
          }
        })

        // suggested tlds
        dispatch({
          type: "RX__tlds_checked",
          data: results.tlds
        })

        // suggested tlds which did not fit, because there are too many
        dispatch({
          type: "RX__tlds_extra",
          data: results.tlds_extra
        })
      })
      .catch(function () {
        // end request
        dispatch({
          type: "RX__in_progress",
          data: false
        })
      })
  }
}
