import cconsoleSetup from "colorful-console-logger"

let CONSOLE =
  typeof window === "object" && window.CONSOLE
    ? window.CONSOLE
    : typeof process === "object" && process.CONSOLE
    ? process.CONSOLE
    : cconsoleSetup({
        useTrace: false,
        useColor: true
      })

if (typeof window === "object") {
  window.CONSOLE = CONSOLE
}
if (typeof process === "object") {
  process.CONSOLE = CONSOLE
}

export default CONSOLE
